.bwm-form {
  form {
    margin-bottom: 10px;
  }

  .alert {
    p {
      margin: 0;
    }
  }

  h1 {
    font-weight: 700;
    margin-bottom: 30px;
  }
  .image-container {
    position: relative;

    img {
      width: 100%;
      border-radius: 5px;
    }
  }

  label {
    font-size: 16px;
  }

  .form-text {
    font-size: 13px;
  }

  .catchphrase {
    text-align: center;
    font-size: 42px;
    position: absolute;
    color: #f1f1f1;
    padding: 40px;
    background-color: rgba(0,0,0, 0.5);
    margin-top: 50px;
    letter-spacing: 1px;
  }
  .react-datepicker-wrapper{
    width:100%;
  } 
  .react-datepicker__input-container{ 
    width:100%;
  }
    
  .react-datepicker-wrapper .react-datepicker__input-container input{
    width:100%;
    border:none;
  }
  .error{
    color:red;
  }
  .rbt input{
    width:100%
  }
  .form-group label:after {
    content:"*";
    color:red;
  }
}  